<template>
    <v-conatiner fluid>
        <v-data-iterator :items="items" :items-per-page.sync="itemsPerPage" :page.sync="page" :search="search" :sort-by="sortBy.toLowerCase()" :sort-desc="sortDesc" hide-default-footer>
            <template v-slot:header>
                <v-toolbar dark color="blue-grey darken-4" class="mb-1">
                <v-text-field v-model="search" clearable flat solo-inverted hide-details prepend-inner-icon="mdi-magnify" label="Szukaj"></v-text-field>
                <template v-if="$vuetify.breakpoint.mdAndUp">
                    <v-spacer></v-spacer>
                    <v-btn-toggle v-model="sortDesc" mandatory>
                    <v-btn large depressed color="grey" :value="false">
                        <v-icon>mdi-arrow-up</v-icon>
                    </v-btn>
                    <v-btn large depressed color="grey" :value="true">
                        <v-icon>mdi-arrow-down</v-icon>
                    </v-btn>
                    </v-btn-toggle>
                </template>
                </v-toolbar>
            </template>

            <template v-slot:default="props">
                <v-row>
                    <v-col v-for="item in props.items" :key="item.name" cols="100" >
                        <v-card width='100vw'>
                            <v-card-title class="subheading font-weight-bold">
                                {{ item.name }}
                            </v-card-title>

                            <v-divider></v-divider>

                            <v-list dense>
                                <v-list-item v-for="(key, index) in filteredKeys" :key="index">
                                <v-list-item-content style='margin: 0' :class="{ 'blue--text': sortBy === key }">
                                    {{ key }}:
                                </v-list-item-content>
                                <v-list-item-content style='margin: 0' :class="{ 'blue--text': sortBy === key }">
                                    {{ item[key.toLowerCase()] }}
                                </v-list-item-content>
                                </v-list-item>
                            </v-list>
                        </v-card>
                    </v-col>
                </v-row>
            </template>

            <template v-slot:footer>
                <v-row class="mt-2 ml-2" align="center" justify="center">
                    <span class="grey--text">Zadania na stronie</span>
                    <v-menu offset-y>
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn dark text color="primary" class="ml-2" v-bind="attrs" v-on="on">
                                {{ itemsPerPage }}
                                <v-icon>mdi-chevron-down</v-icon>
                            </v-btn>
                        </template>
                        <v-list>
                            <v-list-item v-for="(number, index) in itemsPerPageArray" :key="index" @click="updateItemsPerPage(number)">
                                <v-list-item-title>{{ number }}</v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </v-menu>

                    <v-spacer></v-spacer>

                    <span class="mr-4 grey--text">
                        Strona {{ page }} z {{ numberOfPages }}
                    </span>
                    <v-btn fab dark color="grey darken-3" class="mr-1" @click="formerPage">
                        <v-icon>mdi-chevron-left</v-icon>
                    </v-btn>
                    <v-btn fab dark color="grey darken-3" class="ml-1 mr-5" @click="nextPage">
                        <v-icon>mdi-chevron-right</v-icon>
                    </v-btn>
                </v-row>
            </template>
            </v-data-iterator>
    </v-conatiner>
</template>

<script>
export default {
    name: 'TasksList',
    data () {
      return {
        itemsPerPageArray: [1, 5, 10],
        search: '',
        filter: {},
        sortDesc: false,
        page: 1,
        itemsPerPage: 1,
        sortBy: 'nr',
        keys: [
          'Nr',
          'Name',
          'Opis',
          'Termin'
        ],
        items: [
          {
            nr: '1',
            name: 'Kontrolowany atak',
            opis: 'Wykonanie kontrolowanego ataku socjotechnicznego przy pomocy narzdzia SET i Maltego',
            termin: '07-02-2022'
          },
          {
            nr: '2',
            name: 'Kontrolowany atak 2',
            opis: 'Wykonanie kontrolowanego ataku socjotechnicznego przy pomocy narzdzia SET i Maltego. Wykonanie kontrolowanego ataku socjotechnicznego przy pomocy narzdzia SET i Maltego. ',
            termin: '18-02-2023'
          }
        ],
      }
    },
    computed: {
      numberOfPages () {
        return Math.ceil(this.items.length / this.itemsPerPage)
      },
      filteredKeys () {
        return this.keys.filter(key => key !== 'Name')
      },
    },
    methods: {
      nextPage () {
        if (this.page + 1 <= this.numberOfPages) this.page += 1
      },
      formerPage () {
        if (this.page - 1 >= 1) this.page -= 1
      },
      updateItemsPerPage (number) {
        this.itemsPerPage = number
      },
    },
  }
</script>

<style scoped>
    .v-list-item {
        display: block;
    }
</style>